.loader {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    left: 0;
    top: 0;
}

.loader__container {
    background: rgba(255, 255, 255, 0.85);
    -webkit-box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.85);
            box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.85);
    padding: 50px 100px;
    font-size: 2em;
}



